.wrapper{
    --search-boder-radius:92px;
    --search-height:46px;
    --search-top-spacer:9px;
    --seach-button-width:52px;
    
    
    position: fixed;
    top:0;
    background-color: #fff;
    margin-bottom: 20px;
    width: 100%;
    margin-bottom:  var(--default-layout-header-height,60px);
    z-index: 1;
    height: var(--default-layout-header-height,60px);
    box-shadow: 0px 1px 1px rgb(0 0 0 /12%);
    display: flex;
   
}

.inner{
    
    padding: 0 var(--default-layout-horizontal-spacer);
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    display: flex;
    align-items: center;
}

.actions{
    display: flex;
    align-items: center;
}

.more-btn{
    font-size: 2rem;
    margin-left: 28px;
    background-color: transparent;
    cursor: pointer;
    padding: 4px 8px;
}

.action-btn{
    font-size: 2.3rem;
    background-color: transparent;
    color: #161823;
    padding: 4px 10px;
    cursor: pointer;

}

.user-avatar{
    width: 32px;
    height: 32px;
    object-fit: cover;
    border-radius: 50%;
    margin-left: 12px;
    cursor: pointer;
}


