.wrapper {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 9px 16px;
  font-size: 1.6rem;
  font-family: 'ProximaNova', sans-serif;

  border-radius: 4px;
  font-weight: 600;
  min-width: 100px;
  background-color: var(--white);
  border: 1px solid transparent;
  cursor: pointer;
  user-select: none;

  + .wrapper {
    margin-left: 8px;
  }

  &.disabled {
    pointer-events: none;
    opacity: 0.5;
  }
}

//Button style
.rounded {
  border-radius: 999px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 6%);
  border-color: rgba(22, 24, 35, 0.12);

  &:hover {
    border-color: rgba(22, 24, 35, 0.2);
    background-color: rgb(255, 255, 255);
  }
}

.primary {
  background-color: var(--primary);
  color: var(--white);
  border-color: var(--primary);

  &:hover {
    border-color: var(--primary);

    background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), rgb(254, 44, 85);
  }
}

.outline {
  color: var(--primary);
  border-color: currentColor;

  &:hover {
    background-color: rgba(254, 44, 85, 0.06);
    border-color: currentColor;
  }
}

.text {
  &:hover {
    text-decoration: underline;
  }
}

.icon + .title,
.title + .icon {
  margin-left: 8px;
}

//Button size

.small {
  min-width: 88px;
  padding: 4px 16px;
}

.large {
  min-width: 140px;
  padding: 14px 16px;
}
