@import 'normalize.css';
:root{
    --primary:#fe2c55;
    --black:#000;
    --text-color:#161823;
    --white:#fff;
    //DefaultLayout
    --default-layout-header-height:60px;
    --default-layout-width:1150px;
    --default-layout-horizontal-spacer:24px;

}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html{
    font-size: 62.5%;
}

body{
    font-family: 'ProximaNova', sans-serif;
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    color: var(--text-color);

}

button, input, [tabindex]{
    outline: none;
    border: none;
}

a[href]{
    color: var(--text-color);
    text-decoration: none;
}

//Font
@font-face{
    font-family: ProximaNova;
    src: url('/assets/font/Proxima-Nova-Regular.woff2');
    font-weight: 400;
}
@font-face{
    font-family: ProximaNova;
    src: url('/assets/font/ProximaNova-Semibold.woff2');
    font-weight: 600;
    @font-face{
        font-family: ProximaNova;
        src: url('/assets/font/Proxima-Nova-Bold.woff2');
        font-weight: 700;
    }
}
@font-face{
    font-family: SofiaPro;
    src: url('/assets/font/sofiapro-bold.woff2');
    font-weight: 700;
}